.header {
	height: 400px;
	width: 100%;
	display: flex;
	border-bottom: 3px solid var(--green);
	position: absolute;
	z-index: 101;
	top: 0;
}

.wrapper {
	display: flex;
	justify-content: center;
	align-items: center;
	padding: 0px 40px;
	flex: 1 0 0;
	width: 100%;
	position: relative;
}

.background {
	position: absolute;
	width: 100%;
	height: 100%;
	z-index: 999;
}

.logo {
	height: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
	width: 100%;
}

.logo__img {
	height: 100%;
	justify-content: center;
	display: flex;
	width: fit-content;
	padding: 10px 0;
	display: flex;
	flex-direction: column;
	height: 80px;
	align-items: center;
	justify-content: center;
}

.logo__img span:first-child {
	font-family: var(--unbounded);
	font-weight: 600;
	font-size: 40px;
	color: var(--dark-brown);
	border-radius: 20%;
}

.logo__img span {
	display: block;
	z-index: 1;
	/* background: var(--beige); */
	text-shadow:
		var(--beige) 0 0 20px,
		var(--beige) 0 0 20px,
		var(--beige) 0 0 20px,
		var(--beige) 0 0 20px,
		var(--beige) 0 0 20px,
		var(--beige) 0 0 20px,
		var(--beige) 0 0 20px,
		var(--beige) 0 0 20px;
	;
}

.logo__img span:last-child {
	font-family: var(--unbounded);
	font-weight: 400;
	font-size: 18px;
	color: var(--dark-brown);
	border-bottom-left-radius: 50%;
	border-bottom-right-radius: 50%;
	padding: 5px;
}

.menu {
	height: 100%;
	width: 50px;
}

.headerRef {
	transition: all 1s ease-out;
}

.icon_language {
	position: absolute;
	top: 10px;
	right: 10px;
	font-size: 30px;
	color: var(--white);
	cursor: pointer;
}